<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <section class="card-group row w-tab">
    <div class="card col-6">
      <div class="card-body">
        <h4>登入</h4>
        <VForm ref="formRef" v-slot="{ meta }" @submit="logIn">
          <div class="form-floating mb-3">
            <VField name="email" type="email" rules="required|email" class="form-control" />
            <label for="floatingInput"
              >Email address<ErrorMessage name="email" class="text-danger ms-3"
            /></label>
          </div>
          <div class="form-floating mb-3">
            <VField name="password" class="form-control" type="password" rules="required" />
            <label for="floatingInput"
              >Password<ErrorMessage name="password" class="ms-3 text-danger"
            /></label>
          </div>
          <button
            type="submit"
            class="btn"
            :class="{ 'btn-info': !meta.valid }"
            :disabled="!meta.valid"
          >
            送出
          </button>
        </VForm>
      </div>
    </div>
    <div class="card col-6">
      <div class="card-body">
        <h4>註冊</h4>
        <VForm ref="formRef" v-slot="{ errors, meta, resetForm }" @submit="register">
          <div class="form-floating mb-3">
            <VField name="email" type="email" rules="required|email" class="form-control" />
            <label for="floatingInput"
              >Email address<ErrorMessage name="email" class="text-danger ms-3"
            /></label>
          </div>
          <div class="form-floating mb-3">
            <VField name="password" class="form-control" type="password" rules="required|min:6" />
            <label for="floatingInput"
              >Password<ErrorMessage name="password" class="ms-3 text-danger"
            /></label>
          </div>
          <div class="form-floating mb-3">
            <VField
              id="phone"
              name="phone"
              type="text"
              class="form-control"
              :class="{ 'is-invalid': errors['電話'] }"
              :rules="isPhone"
              v-model="user.phone"
            />
            <label for="phone"
              >phone<error-message name="phone" class="text-danger ms-3"></error-message
            ></label>
          </div>
          <div class="form-floating mb-3">
            <VField
              name="birth"
              class="form-control"
              type="text"
              rules="required"
              v-model="user.birth"
            >
              <VueDatePicker
                v-model="user.birth"
                placeholder="Birthday Day"
                format="yyyy-MM-dd"
                :flow="flow"
                :enable-time-picker="false"
                :max-date="new Date()"
              ></VueDatePicker>
            </VField>
          </div>
          <div class="d-flex justify-content-between">
            <button
              type="submit"
              class="btn"
              :class="{ 'btn-info': !meta.valid }"
              :disabled="!meta.valid"
            >
              送出
            </button>
            <button type="button" class="btn btn-outline-danger" @click="resetForm">重設</button>
          </div>
        </VForm>
      </div>
    </div>
  </section>
</template>

<script>
// eslint-disable-next-line import/no-extraneous-dependencies
import VueDatePicker from '@vuepic/vue-datepicker';
// eslint-disable-next-line import/no-extraneous-dependencies
import '@vuepic/vue-datepicker/dist/main.css';

export default {
  data() {
    return {
      user: {
        username: null,
        email: '',
        password: '',
        birth: '',
        phone: '',
        gender: '不提供',
        address: {
          city: null,
          town: null,
          postalCode: null,
          street: null,
        },
      },
      flow: ['year', 'month', 'calendar'],
    };
  },
  components: { VueDatePicker },
  methods: {
    isPhone(value) {
      const phoneNumber = /^(09)[0-9]{8}$/;
      return phoneNumber.test(value) ? true : '需要正確的電話號碼';
    },
    logIn(e) {
      const api = `${process.env.VUE_APP_API2}/api/user/signIn`;
      this.$http
        .post(api, e)
        .then((res) => {
          if (res.data.status) {
            const { token, expired } = res.data.data;
            document.cookie = `LuluBakeToken=${token};expires=${new Date(expired)}`;
            this.$router.push('/');
          } else {
            // eslint-disable-next-line no-alert
            alert(res.data.message);
          }
        })
        .catch((error) => {
          if (error.response) {
            this.errorMessage = error.response.data.message;
            console.log(error.response.data);
          }
        });
    },
    register(e) {
      const api = `${process.env.VUE_APP_API2}/api/user/signUp`;
      this.$http
        .post(api, e)
        .then((res) => {
          console.log(res);
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>
